.galeria .videos .video iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.galeria .imagens {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-left: -2%;
}

.galeria .imagens .imagem {
    margin: 0 0 2% 2%;
    padding-top: 18%;
    width: calc(25% - 2%);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

@media screen and (max-width:992px) {
    .galeria .imagens .imagem {
        padding-top: 30%;
        width: calc(50% - 2%);
    }
}